.applyWrap {
    padding-top: 150px;
    display: flex;
    min-height: 800px;
    h4 {
        font-size: 18px;
        padding: 5px 0 5px 5px;
        border-bottom: 3px solid #FFA300;
        span {
            margin: 5px;
            font-size: 12px;
            color: #666;
        }
    }
    .left {
        width: 20%;
        padding: 0 20px 0 80px;
        ul {
            li {
                cursor: pointer;
                font-size: 14px;
                font-weight: 700;
                line-height: 2.5;
                padding-left: 5px;
                border-bottom: 1px solid #eee;
                color: #333;
                &:hover {
                    color: #FFA300;
                }
            }
        }
    }
    .right {
        width: 80%;
        padding: 0 80px 0 20px;
        margin-bottom: 120px;
        .form {
            margin: 15px 0 0 5px;
            h5 {
                font-size: 16px;
                span {
                    font-size: 12px;
                    color: #666;
                }
            }
            form {
                margin: 10px 0 30px;
                .w60 {
                    display: inline-block;
                    width: 60%;
                }
                .w15 {
                    display: inline-block;
                    width: 14.25%;
                }
                .w20 {
                    display: inline-block;
                    width: 19%;
                }
                .w50 {
                    display: inline-block;
                    width: 49.5%;
                }
                .r1 {
                    margin-right: .5%;
                }
                .l1 {
                    margin-left: .5%;
                }
                .l2 {
                    margin-left: 1%;
                }
                .ex {
                    margin: 0 0 15px 10px;
                    font-size: 14px;
                    color: #666;
                }
            }
            textarea {
                width: 100%;
                height: 120px;
                padding: 10px;
                margin: 10px 0;
            }
        }
        .btn {
            text-align: center;
            margin-top: 30px;
            button {
                background-color: #FFA300;
                padding: 10px 30px;
                color: #fff;
                border-radius: 50px;
                font-weight: bold;
                font-size: 16px;
            }
        }
    }
}