    }
    .article2 {
        margin: 150px 0 50px;
        display: flex;
        flex: 1 1 1;
        .table {
            display: inline-block;
            width: 32.33%;
            border-top: 2px solid #ddd;
            border-bottom: 2px solid #ddd;
            padding: 15px 0 20px;
            margin: 0 0.5%;
            .noticeTitle {
                display: flex;
                justify-content: space-between;
                padding: 10px 0;
                h6 {
                    font-size: 20px;
                    margin: 0 20px;
                    border-bottom: 4px solid #FFA300;
                    color: #333;
                }
                span {
                    font-size: 14px;
                    padding: 10px 20px 5px;
                }
            }
            table {
                tr {
                    td {
                        padding: 10px 20px;
                        font-size: 14px;
                        text-overflow:ellipsis;
                        white-space:nowrap;
                        word-wrap:normal;
                        overflow:hidden;
                        color: #333;
                    }
                    td:first-of-type {
                        width: auto;
                        &:hover {
                            color: #FFA300;
                        }
                    }
                    td:last-of-type {
                        width: 120px;
                        text-align: right;
                    }
                }
            }
        }
        .video {
            iframe {
                width: 100%;
                height: 84%;
                padding: 10px 20px;
            }
        }
    }
}