.artistWrap {
    .intro {
        .profileSec {
            margin-top: 15px;
            margin-bottom: 100px;
            display: grid;
            grid-template-columns: auto 65%;
            column-gap: 20px;
            .imgbox {
                background: #29918A;
                width: 350px;
                height: 350px;
                position: relative;
                img {
                    position: absolute;
                    top: 50%;
                    transform: translateY(-50%);
                }
            }
            .textbox {
                margin-top: 40px;
                h3 {
                    font-size: 24px;
                    margin-bottom: 30px;
                    span {
                        font-size: 14px;
                        color: #888;
                    }
                }
                .profList {
                    li {
                        font-size: 14px;
                        font-weight: bold;
                        color: #29918A;
                        span {
                            color: #333;
                            font-weight: normal;
                        }
                        p {
                            padding-top: 5px;
                            color: #333;
                            font-weight: normal;
                        }
                    }
                }
            }
        }
        .secTit {
            display: flex;
            justify-content: space-between;
            border-bottom: 3px solid #FFA300;
            h4 {
                font-weight: bold;
                font-size: 18px;
                border: none;
                span {
                    color: #666;
                    font-size: 18px;
                }
            }
            p {
                color: #444;
                font-weight: 600;
                font-size: 14px;
                padding: 5px 0;
            }
        }
        .albumSec {
            margin-bottom: 100px;
            .albumList {
                margin-top: 20px;
                display: grid;
                grid-template-columns: 25% 25% 25% 25%;
                column-gap: 20px;
                li {
                    h6 {
                        margin: 10px 0 5px;
                        font-size: 14px;
                        font-weight: bold;
                        white-space: nowrap;
                        overflow: hidden;
                        text-overflow: ellipsis;
                    }
                    p {
                        font-size: 14px;
                        line-height: 1.5;
                    }
                }
            }
        }
        .trackListSec {
            margin-bottom: 100px;
            .trackList {
                .track {
                    li {
                        display: grid;
                        grid-template-columns: 85% auto;
                        padding: 10px 0;
                        border-bottom: 1px solid #eee;
                        h6, p {
                            font-size: 15px;
                            text-align: left;
                            padding-left: 5px;
                        }
                    }
                }
            }
        }
    }
}