.navi {
    position: fixed;
    top: 100px;
    right: -170px;
    z-index: 99999;
    transition: 0.5s;
    &:hover {
        right: 0;
    }
    .lnk {
        padding: 15px 25px;
        display: flex;
        background: #FFA300;
        color: #fff;
        border-radius: 50px 0 0 50px;
        font-weight: bold;
        .icon {
            font-size: 22px;
        }
    }
}