.artistWrap {
    padding-top: 150px;
    display: flex;
    min-height: 800px;
    margin-bottom: 150px;
    h4 {
        font-size: 18px;
        padding: 5px 0 5px 5px;
        border-bottom: 3px solid #FFA300;
        span {
            margin: 5px;
            font-size: 12px;
            color: #666;
        }
    }
    .left {
        width: 20%;
        padding-right: 20px;
        ul {
            li {
                cursor: pointer;
                font-size: 14px;
                font-weight: 700;
                line-height: 2.5;
                padding-left: 5px;
                border-bottom: 1px solid #eee;
                color: #333;
                &:hover {
                    color: #FFA300;
                }
            }
        }
    }
    .right {
        width: 100%;
        padding: 0 80px 0 20px;
        .list {
            display: grid;
            grid-template-columns: auto auto auto auto;
            column-gap: 20px;
            row-gap: 60px;
            margin-top: 5px;
            li {
                margin: 10px 0;
                h5 {
                    margin-top: 10px;
                    text-align: center;
                    font-weight: normal;
                }
                div {
                    position: relative;
                    .overlay{
                        position: absolute;
                        overflow: hidden;
                        top: 0; left: 0; right: 0; bottom: 0;
                        background: rgba($color: #000000, $alpha: 0.5);
                        opacity: 0;
                        transition: 0.5s;
                        .detail {
                            position: absolute;
                            font-size: 18px;
                            transform: translate(-50%, -50%);
                            left: 50%;
                            top: 50%;
                            color: #fff;
                        }
                        &:hover {
                            cursor: pointer;
                            opacity: 1;
                        }
                    }
                }
            }
        }
    }
}