.auditionWrap {
    padding-top: 150px;
    display: flex;
    min-height: 800px;
    h4 {
        font-size: 18px;
        padding: 5px 0 5px 5px;
        border-bottom: 3px solid #FFA300;
        span {
            margin: 5px;
            font-size: 12px;
            color: #666;
        }
    }
    .left {
        width: 20%;
        padding: 0 20px 0 80px;
        ul {
            li {
                cursor: pointer;
                font-size: 14px;
                font-weight: 700;
                line-height: 2.5;
                padding-left: 5px;
                border-bottom: 1px solid #eee;
                color: #333;
                &:hover {
                    color: #FFA300;
                }
            }
        }
    }
    .right {
        width: 80%;
        padding: 0 80px 0 20px;
        .info {
            position: relative;
            margin-bottom: 100px;
            .imgbox {
                margin: 20px 0 50px;
                height: 300px;
                overflow: hidden;
                position: relative;
                background-position: center;
                img {
                    position: relative;
                    height: auto;
                    opacity: 0.5;
                    top: -40%;
                }
                h3 {
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    font-size: 36px;
                    border: 10px solid #FFA300;
                    padding: 10px 30px;
                    border-radius: 50px;
                }    
            }
            h5 {
                font-size: 16px;
                margin: 0 0 10px 5px;
                img {
                    width: 22px;
                    margin-right: 8px;
                }
            }
            p {
                font-size: 14px;
                margin: 0 0 50px 30px;
                line-height: 1.5;
            }
            .btn {
                text-align: center;
                button {
                    background-color: #FFA300;
                    padding: 10px 30px;
                    color: #fff;
                    border-radius: 50px;
                    font-weight: bold;
                    font-size: 16px;
                }
            }
        }
    }
}