.footerWrap {
    height: 200px;
    .row {
        display: grid;
        grid-template-columns: 130px auto 300px;
        column-gap: 50px;
        .logo {
            img {
                width: 130px;
            }
        }
        .info {
            p {
                font-size: 14px;
                line-height: 1.8;
                color: #333;
            }
        }
        .sns {
            h6 {
                font-size: 14px;
                margin-bottom: 10px;
            }
            .icon {
                display: grid;
                grid-template-columns: auto auto auto auto auto auto auto;
                column-gap: 5px;
                margin-bottom: 15px;
                img {
                    width: 22px;
                }
            }
        }
    }
}