.headerWrap {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 9999;
    background: #fff;
    color: #666;
    font-size: 14px;
    .row {
        display: flex;
        height: 80px;
        justify-content: space-between;
        position: relative;
        align-items: center;
        .logo {
            img {
                width: 130px;
            }
        }
        .nav {
            /* position: relative; */
            /* text-align: right; */
            cursor: pointer;
            position: absolute;
            top: 50%; left: 50%;
            transform: translate(-50%, -50%);
            .depth1 {
                li {
                    display: inline-block;
                    position: relative;
                    padding: 10px 40px;
                    font-weight: bold;
                }
            }
            .depth2 {
                display: none;
                position: absolute;
                top: 100%;
                left: 0;
                text-align: left;
                width: 180px;
                height: 200px;
                background: #fff;
                font-size: 12.5px;
                &.on {
                    display: block;
                }
                li {
                    display: inline-block;
                    font-weight: normal;
                    transition: 0.5s;
                    &:hover {
                        color: #FFA300;
                        text-indent: 0.5em;
                    }

                }
            }
        }
        .lang {
            display: inline-block;
            button {
                background: none;
                border: none;
                margin: 0 5px;
                font-size: 12px;
                color: #666;
                &.on {
                    color: darkslateblue;
                }
            }
            button:first-of-type {
                color: darkslateblue;
                font-weight: bold;
            }
        }
    }
}