body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none;
  font-family: 'Alatsi', sans-serif;
}
a {
  color: inherit;
  text-decoration: none;
}
ul, ol {
  list-style: none;
}
.row {
  max-width: 1600px;
  margin: 0 auto;
  padding: 0 80px;
}
.cf:after {
  content: '';
  display: block;
  clear: both;
}
img {
  width: 100%;
  vertical-align: middle;
}
button {
  cursor: pointer;
  border: none;
}
fieldset {
  display: none;
}
legend {
  display: none;
}
h1, h2, h3, h4, h5, h6 {
  font-weight: 100%;
}
table {
  width: 100%;
  border-collapse: collapse;
  table-layout: fixed;
}
.blind {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  text-indent: -99999px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
